.graph {
    flex: 0 1 45%;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0 5px 15px;
    border-radius: 12px;
    padding: 20px;
    margin: 10px 0;

    max-width: 695px;
}
.graph_title {
    padding-bottom: 10px;
    border-bottom: 1px solid black;
    margin-bottom: 10px;
}

.graph_line {
    width: 100% !important;
}

.labels {
    display: flex;
    justify-content: space-between;
    align-items: center;

    font-size: 18px;
}

.space_bottom {
    margin-bottom: 10px;
}


.period {
    border-top: 1px solid black;
    padding-top: 10px;
    margin-top: 10px;

    text-align: center;
}

@media only screen and (max-width: 1020px) {
    .graph {
        flex: 0 1 100%;
        margin: 10px auto;
    }
}
@media only screen and (max-width: 565px) {
    .labels {
        font-size: 15px;
    }
}

@media only screen and (max-width: 420px) {
    .labels {
        display: block;
    }
    .labels > span {
        display: flex;
        align-items: center;
    }

    .labels > span h1{
        margin-right: 20px;
    }

    .labels > span h4{
        font-size: 16px;
        padding-bottom: 8px;
    }
}