.home_container {
  display: grid;
  grid-template-columns: 320px auto;
}

.horizontal_side_panel_fs {
  width: 90% !important;
  transition: transform 0.6s ease-out !important;
}

.horizontal_side_panel_fs > button {
  width: 36px;
  color: black;
  background-color: white;
  --contentButton: 'Γραφήματα';
}

.horizontal_side_panel_tableau {
  width: 90% !important;
  background-color: white !important;
}

.horizontal_side_panel_tableau > button {
  top: calc(50% + 36px);
  width: 36px;
  background-color: #102232;
  --contentButton: 'Χρονοσειρές';
}

@media only screen and (max-width: 991px) {
  .home_container {
    grid-template-columns: 100%;
    grid-template-areas: "welcome" "box_placeholder";
  }
}

@media only screen and (max-width: 609px) {
  .horizontal_side_panel_tableau,
  .horizontal_side_panel_fs {
    width: 100% !important;
  }

  .horizontal_side_panel_fs > button {
    top: 100%;

    left: calc(50% - 18px);
    transform: translate(0%, -8px) rotate(90deg);
  }

  .horizontal_side_panel_tableau > button {
    top: 100%;

    left: calc(50% - 72px);
    transform: translate(0%, -8px) rotate(90deg);
  }

}
